.footer {
  background-color: #84A178;
  padding-top: 60px;
}

.footer-container{
  border-bottom: 1px solid #E1E1E1;
  display: flex;
  @media screen and (max-width:767px) {
    flex-wrap: wrap;
  }
}
.footer-first-container{
  margin-right: auto;
}
.footer-second-container {
  margin-left: auto;
  @media screen and (min-width: 768px) and (max-width: 1199px) {
    padding-top: 80px;
  }
}
.footer-title {
   font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 18px;
    line-height: 1.22;
    text-transform: uppercase;
  
    color: #FCFCFC;

    margin-bottom: 16px;
    max-width: 230px;
    @media screen and (min-width: 768px) {
      
        font-size: 26px;
        line-height: 1.23;
        letter-spacing: 0.04em;
        margin-bottom: 9px;
        max-width: 350px;
    }

    @media screen and (min-width: 1200px) {
    
        font-weight: 400;
        font-size: 30px;
        line-height: 1.23;
        margin-bottom: 14px;
        max-width: 100%;
    }
  
}

.footer-subtitle{

  font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 34px;
    line-height: 1.20;
    letter-spacing: 0.04em;
    text-transform: uppercase;
  
    color: #FCFCFC;

    margin-bottom: 40px;

    max-width: 170px;
    @media screen and (min-width: 768px) {

     
        font-size: 48px;
        line-height: 1.22;
        letter-spacing: 0.06em;
        margin-bottom: 32px;
        max-width: 240px;
    }

        @media screen and (min-width: 1200px) {

            font-size: 58px;
            line-height: 1.22;
            letter-spacing: 0.06em;
            margin-bottom: 54px;
            max-width: 100%;
        }
}

.footer-list{
  display: flex;
  gap: 30px;
  justify-content: end;
  margin-bottom: 38px;

  @media screen and (min-width: 768px) {
    
    margin-bottom: 32px;
  }
     @media screen and (min-width: 1200px){

      margin-bottom: 40px;
     }
}
.footer-list__item {
width: 34px;
height: 34px;
border-radius: 50%;
}

.footer-list__link {
background-color: #F8D24E;
color: #ffffff;
display: flex;
  justify-content: center;
  align-items: center;
  text-decoration: none;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1), color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  
  &:hover, &:focus {
    color: #84A178;
  }
}

.footer-list__icon {
  fill: currentColor;
}

.contact{

  font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 12px;
    line-height: 1.86;
    text-align: right;
  
    color: #131313;
    margin-bottom: 40px;

    @media screen and (min-width: 768px) {

        
          font-size: 14px;
          line-height: 1.86;
          margin-bottom: 56px;
      }
    @media screen and (min-width: 1200px) {
        font-size: 16px;
        line-height: 1.86;
        letter-spacing: 0.02em;
        margin-bottom: 60px;
             }
}
.contact__number{
    color: #FCFCFC;    
}

.footer-copyright{

    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 1.52;
    text-align: center;
    margin-left: auto;
    margin-right: auto;
    color: #7A7A7A;

    @media screen and (max-width: 767px) {
      max-width: 150px;
    }

    @media screen and (min-width: 768px) {
  
   
     font-size: 14px;
     line-height: 1.86;
     letter-spacing: 0.02em;
    }

     @media screen and (min-width: 1200px){
     font-size: 12px;
     line-height: 1.86;
     letter-spacing: 0.02em;

    }
}
.copyright {
  padding-top: 12px;
  padding-bottom: 62px;
  @media screen and (min-width: 768px) {
    padding-bottom: 12px;
  }
}